<template>
    <div class="search-index">
        <div class="search van-hairline--bottom">
            <div class="back" @click="$routerBack">
                <div class="ico"></div>
            </div>
            <div class="input">
                <van-field ref="input" @keyup.enter="searchHandel" v-model="search.value" maxlength="14" placeholder="" clearable/>
            </div>
            <div class="text" @click="searchHandel({})">
                
            </div>
        </div>
        <div class="history" v-if="history.data.length > 0">
            <div class="x-title">
                <div class="text">
                    
                </div>
                <div class="clean" @click="historyClean">
                    <van-icon name="delete"/>
                </div>
            </div>
            <div class="box">
                <div class="item" v-for="(item,index) in history.data" :key="index" @click="searchHandel(item)">
                    {{ item }}
                </div>
            </div>
        </div>
        <div class="hot">
            <div class="x-title"></div>
            <div class="box">
                <div class="item" v-for="(item,index) in hot.data" :key="index" @click="searchHandel(item.title)">
                    {{ item.title }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {Icon, Field} from 'vant';

    export default {
        components: {
            [Icon.name]: Icon,
            [Field.name]: Field
        },
        data() {
            return {
                search: {
                    value: ''
                },
                history: {
                    data: []
                },
                hot: {
                    data: []
                }
            }
        },
        mounted() {
            this.initHistory();
            this.initHot();
        },
        activated() {
            this.search.value = this.$store.state.searchKeyword;
            this.$refs.input.focus();
        },
        methods: {
            initHot() {
                this.$axios.get('/api/app/search/keyword')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.hot.data = result;
                        }
                    });
            },
            initHistory() {
                if (localStorage.history) {
                    this.history.data = localStorage.history.split(',');
                }
            },
            searchHandel(value = null) {
                let keyword = typeof value === 'object' ? this.search.value : value;
                this.$store.state.searchKeyword = keyword;
                this.historyCreate(keyword);
                this.$router.push('/search/data?keyword=' + keyword);
            },
            historyCreate(value) {
                let data = localStorage.history;
                // 
                if (data !== undefined) {
                    data = data.split(',');
                    const index = data.indexOf(value);
                    if (index === -1) {
                        data.unshift(value);
                    }
                    if (index !== -1) {
                        data.splice(index, 1);
                        data.unshift(value);
                    }
                }
                // 
                if (data === undefined) {
                    data = [];
                    data.unshift(value);
                }
                this.history.data = localStorage.history = data;
            },
            historyClean() {
                localStorage.removeItem('history');
                this.history.data = [];
            }
        }
    }
</script>

